:root{
	font-size: 10px;
}

*,
*:before,
*:after{
	box-sizing: border-box;
}

body{
	overflow-x: hidden;
	margin: 0;
	font-family: 'Montserrat', sans-serif;
	font-size: 2.2rem;
	background: #E5E5E5;
}

a{
	text-decoration: none;
	color: #000;
}

ul{
	padding: 0;
	margin: 0;
}

.button{
	background: none;
	outline: none;
	cursor: pointer;
	border: none;
	transition: .2s linear;
}

.input{
	outline: none;
	border: none;
	background: #F2F2F2;
	width: 100%;
	padding: 1.5rem 2rem;
	margin-top: .5rem;
}

.container{
	max-width: 1600px;
	width: 100%;
	margin: 0 auto;
}

// Главная

.header{
	width: 100%;
	background: linear-gradient(to right, #DDDDDD 55%, #424141 45%);
	padding: 1.5rem 0;
}

.header__inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__nav--inner{
	width: 56%;
	display: flex;
	align-items: center;
}

.header__nav--inner a{
	font-weight: 600;
	margin-right: 9rem;
	transition: .2s linear;
}

.header__nav--inner a:hover{
	color: #959595;
}

.header__nav--inner a:last-child{
	margin-right: 0;
}

.fas.mobile__menu--icon{
	display: none;
}

.header__cart--inner{
	width: 41%;
	display: flex;
	align-items: center;
}

.header__search--inner{
	position: relative;
	margin-right: 6.9rem;
}

.header__search{
	width: 440px;
	border-radius: .3rem;
	background: #4B4B4B;
	font-size: 2.2rem;
	padding: 1rem 4.5rem 1rem 2rem;
	color: #fff;
}

.header__search--icon{
	color: #8C8C8C;
	position: absolute;
	right: 15px;
	top: 50%;
	transform: translateY(-50%);
}

.header__cart--box{
	display: flex;
	align-items: center;
	cursor: pointer;
	color: #fff;
}

.header__cart--box:hover{
	color: #959595;
}

.header__cart--icon{
	margin-right: 1rem;
	transition: .2s linear;
}

.header__cart--text{
	font-weight: 600;
	transition: .2s linear;
}

// Блок лого

.logo{
	width: 100%;
	padding: 1.5rem 0 1rem 0;
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	background: #fff;
}

.logo__inner{
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.logo__box, .logo__pats--inner{
	display: flex;
	align-items: center;
}

.logo__img--inner{
	width: 295px;
	height: auto;
	margin-right: 2.5rem;
}

.logo__img{
	width: 100%;
	height: auto;
}

.logo__text{
	font-size: 2.4rem;
	max-width: 265px;
	color: #B0B0B0;
	font-weight: 800;
}

.logo__pats--item{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-right: 10rem;
	transition: .2s linear;
	cursor: pointer;
}

.logo__pats--item:hover{
	opacity: .7;
}

.logo__pats--item:last-child{
	margin-right: 0;
}

.dog__img--box{
	width: 90px;
	height: auto;
}

.cat__img--box{
	width: 70px;
	height: auto;
}

.logo__pats--item--img{
	width: 100%;
	height: auto;
}

.logo__pats--item--text{
	color: rgba(0, 0, 0, 0.7);
	font-weight: 600;
}

// Главная часть

.main{
	background: url("../img/header-bg.png") center no-repeat;
	background-size: cover;
	padding: 12.3rem 0;
}

.main__inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.main__suptitle{
	font-size: 4.7rem;
	font-weight: 800;
	color: #fff;
	text-shadow: 0 0 10px #959595;
}

.main__title{
	font-weight: 800;
	font-size: 6.9rem;
	color: #F90;
	margin-top: -1.5rem;
}

.main__subtitle{
	font-size: 3.9rem;
	font-weight: 700;
	color: #fff;
	text-shadow: 0 0 10px #959595;
}

.main__point--inner{
	display: flex;
	align-items: center;
	margin-top: 10rem;
}

.main__button{
	margin-top: 6rem;
	padding: 2.8rem 5rem;
	background: #FF9900;
	color: #fff;
	font-weight: 700;
	font-size: 2.7rem;
}

.main__button:hover{
	background: darken(#FF9900, 8);
}

.main__point--item{
	display: flex;
	align-items: center;
	margin-right: 2rem;
}

.main__point--img--inner{
	width: 70px;
	height: auto;
	margin-right: 1rem;
}

.main__point--img{
	width: 100%;
	height: auto;
}

.main__point--text{
	color: #fff;
	max-width: 160px;
	font-weight: 600;
	text-shadow: 0 0 10px #959595;
}

// Контент главной

.content{
	padding: 6rem 0;
	background: #E5E5E5;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.content__inner{
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 100%;
}

.content__main--img--inner{
	width: 100%;
	height: auto;
	margin-top: 3rem;
}

.content__main--img{
	width: 100%;
	height: auto;
}

.content__main--img--inner:first-child{
	margin-top: 0;
}

.content__title{
	color: rgba(0, 0, 0, 0.7);
	font-size: 3.2rem;
	font-weight: 800;
	margin-top: 7rem;
	display: flex;
	align-items: center;
}

.content__title:first-child{
	margin-top: 0;
}

.content__title--icon{
	margin-left: 1.5rem;
	position: relative;
	top: 2px;
}

.choose__color--button{
	cursor: pointer;
	transition: .2s linear;
}

.card__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	flex-wrap: wrap;
}

.card__item{
	background: #fff;
	display: flex;
	flex-direction: column;
	padding: 2.3rem 2.7rem;
	width: 23%;
	margin: 2rem 2rem 0 0;
}

.card__item--img--inner, .card__item--img{
	width: 100%;
	height: auto;
}

.card__price--inner{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 1rem;
}

.card__price{
	color: #E4B12C;
	font-size: 2.5rem;
	font-weight: 600;
}

.card__size{
	font-size: 2.1rem;
	color: #C2C2C2;
	font-weight: 500;
}

.size__bold{
	font-size: 2.7rem;
	color: #505050;
	font-weight: 500;
}

.card__title{
	margin-top: 1rem;
	color: #000;
	font-weight: 600;
}

.card__rating--inner{
	margin-top: 1rem;
	width: 105px;
	height: auto;
}

.card__desc{
	font-size: 1.4rem;
	margin-top: 1rem;
}

.card__color--inner{
	display: flex;
	align-items: center;
	width: 100%;
	flex-wrap: wrap;
}

.card__color--item{
	width: 35px;
	height: 35px;
	border-radius: 50%;
	background: #000;
	margin: 1rem 1rem 0 0;
	cursor: pointer;
}

.card__darkgrey{
	background: #2B2C31;
}

.card__lightbrown{
	background: #D5A280;
}

.card__brown{
	background: #7B543F;
}

// Хлебные крошки

.breadcrops{
	padding: 2.3rem 0;
	background: #fff;
}

.breadcrops__inner{
	width: 100%;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.breadcrops__item--icon{
	margin-right: 1rem;
	font-size: 1.2rem;
	color: #C1C1C1;
}

.breadcrops__item{
	margin-right: 1rem;
	font-size: 2rem;
	color: #C1C1C1;
	font-weight: 500;
}

.breadcrops__active{
	color: #F90;
}

// Комментарии

.comment{
	padding: 3rem 0;
}

.comment__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
}

.comment__item{
	width: 33%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 1rem;
	margin-top: 4rem;
}

.comment__img--inner{
	width: 150px;
	height: 150px;
	border-radius: 50%;
	overflow: hidden;
	flex-shrink: 0;
}

.comment__img{
	width: 150%;
	height: auto;
}

.comment__text{
	font-size: 1.3rem;
	margin-left: 2rem;
}


// Карточка товара

.tovar{
	padding: 2rem 0;
	background: #E5E5E5;
}

.tovar__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
}

.tovar__desc--inner{
	width: 56%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-right: 2rem;
}

.tovar__desc--box{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.tovar__desc--title{
	font-size: 2.4rem;
	color: #A8A8A8;
	font-weight: 800;
	margin-top: 3rem;
}

.tovar__desc--text{
	font-size: 1.5rem;
	margin-top: 1rem;
}

.tovar__value--inner{
	width: 44%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-left: 2rem;
}

.tovar__value--title{
	font-size: 2.7rem;
	color: rgba(0, 0, 0, 0.7);
	font-weight: 800;
}

.tovar__value--price{
	font-weight: 600;
	color: #E4B12C;
	font-size: 3.5rem;
}

.tovar__value--point--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.tovar__bold{
	font-weight: 800;
}

.tovar__value--point{
	font-size: 1.7rem;
	color: #888;
	font-weight: 500;
	margin-top: 2rem;
}

.tovar__value--button--size{
	margin-top: 3.7rem;
	font-size: 1.8rem;
	padding: 1.5rem 8rem;
	border: 1px solid #000;
}

.tovar__value--button--size:hover{
	background: #000;
	color: #fff;
}

.tovar__value--color--inner{
	width: 100%;
	margin-top: 4.5rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.tovar__value--color--title{
	font-size: 2rem;
	font-weight: 800;
	color: #4D4D4D;
}

.tovar__value--color--box{
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	width: 100%;
}

.tovar__value--color--item{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 2rem 2rem 0 0;
	cursor: pointer;
	max-width: 100px;
	width: 100%;
	text-align: center;
}

.tovar__value--color--bg{
	width: 60px;
	height: 60px;
	background: #000;
}

.tovar__value--cart--box{
	display: flex;
	align-items: center;
	margin-top: 4rem;
}

.tovar__lightbrown--bg{
	background: #D5A280;
}

.tovar__dark--bg{
	background: #2B2C31;
}

.tovar__brown--bg{
	background: #7B543F;
}

.tovar__lightbrown--dark--horizontal--bg{
	background: linear-gradient(#2B2C31 50%, #D5A280 50%);
}

.tovar__lightbrown--dark--vertical--bg{
	background: linear-gradient(to right, #D5A280 50%, #2B2C31 50%);
}

.tovar__value--color--text{
	color: #888;
	font-size: 1.2rem;
	margin-top: .5rem;
}

.tovar__value--button--addcart{
	color: #fff;
	font-size: 2.4rem;
	font-weight: 800;
	padding: 1.8rem 4.6rem;
	background: #FF9900;
	margin-right: 2rem;
}

// Карусель товара

.tovar__carousel--inner{
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}

.tovar__carousel--min{
	width: 18%;
	position: relative;
	display: flex;
	flex-direction: column;
}

.tovar__carousel--max{
	width: 80%;
}

.tovar__carousel--max--item{
	width: 100%;
	height: auto;
}

.tovar__carousel--max--img{
	width: 100%;
	height: auto;
}

.tovar__carousel--min--item{
	width: 155px!important;
	height: auto!important;
}

.tovar__carousel--min--img{
	width: 100%;
	height: auto;
}

.carousel__min--arrow{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	font-size: 3rem;
	color: #888888;
	z-index: 5;
}

.carousel__min--arrow--next{
	bottom: -35px;
}

.carousel__min--arrow--prev{
	top: -35px;
}

.maps__frame{
	width: 100%;
	height: 750px;
}

// Популярные товары

.popular{
	padding: 4rem 0;
	background: #E5E5E5;
}

.card__inner.popular__carousel{
	width: 80%;
	margin: 0 auto;
}

.popular__arrow{
	width: 84px;
	height: 84px;
	background: #F0F0F0;
	border-radius: 50%;
	display: flex!important;
	align-items: center!important;
	justify-content: center!important;
	color: darken(#F0F0F0, 15);
	font-size: 5rem;
	transition: .2s linear;
	position: absolute;
	top: 50%;
	z-index: 5;
	transform: translateY(-50%);
}

.popular__arrow--next{
	right: -120px;
}

.popular__arrow--prev{
	left: -140px;
}

.popular__arrow:hover{
	opacity: .7;
}

// О нас

.about{
	padding: 4rem 0;
}

.about__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
}

.about__ava--inner{
	width: 11%;
}

.about__text--inner{
	width: 89%;
	display: flex;
	flex-direction: column;
	padding: 0 2rem;
}

.about__ava--img--inner{
	width: 165px;
	height: 165px;
	border-radius: 50%;
	border: 9px solid rgba(0, 0, 0, 0.2);
	overflow: hidden;
}

.about__ava--img{
	width: 130%;
	height: auto;
}

.about__text--container{
	padding: 2rem 1rem;
	border-radius: 5px;
	border: 9px solid rgba(0, 0, 0, 0.2);
	background: #fff;
	color: #A4A4A4;
	font-size: 2.2rem;
	font-weight: 800;
	margin-top: 8rem;
}

.about__text--container:first-child{
	margin-top: 0;
}

.about__text--box{
	margin-top: 4rem;
}

.about__text--box:first-child{
	margin-top: 0;
}

.about__img--inner{
	padding: 3rem 0;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.about__img--box{
	width: 545px;
	height: auto;
}

.about__img{
	width: 100%;
	height: auto;
}

// Доставка

.delivery{
	padding: 3rem 0;
}

.delivery__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
}

.delivery__text--inner{
	width: 55%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding-left: 2rem;
}

.delivery__title{
	color: rgba(0, 0, 0, 0.78);
	text-transform: uppercase;
	font-weight: 700;
	font-size: 2.1rem;
	margin-top: 4rem;
}

.delivery__title:first-child{
	margin-top: 0;
}

.delivery__text{
	margin-top: 1rem;
	color: #A8A8A8;
	font-size: 600px;
	font-size: 1.6rem;
	padding-left: 3rem;
}

.delivery__img--inner{
	width: 45%;
	height: auto;
}

.delivery__img{
	width: 100%;
	height: auto;
	opacity: .4;
}

// Корзина

.cart{
	padding: 3rem 0;
}

.cart__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.cart__item{
	width: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	padding: 3rem 0;
	border-bottom: 1px solid #000;
	flex-wrap: wrap;
}

.cart__item--img--inner{
	width: 190px;
	height: auto;
	margin-right: 5.5rem;
}

.cart__item--img{
	width: 100%;
	height: auto;
}

.cart__view{
	font-size: 1.6rem;
	color: #000;
}

.cart__item--name--inner, .cart__item--price--inner,
.cart__item--totalprice--inner, .cart__item--delete--inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 1rem;
}

.cart__item--name--inner{
	width: 100%;
	max-width: 270px;
	margin-right: 1rem;
}

.cart__item--price--inner{
	width: 100%;
	max-width: 190px;
	margin-right: 1rem;
}

.cart__item--totalprice--inner{
	width: 100%;
	max-width: 170px;
	margin-right: 1rem;
}

.cart__name--title{
	margin-top: 2rem;
	font-weight: 800;
	color: #545454;
	font-size: 1.8rem;
}

.cart__name--point{
	color: #9E9E9E;
	font-size: 1.4rem;
	font-weight: 600;
}

.cart__price{
	margin-top: 2rem;
	font-weight: 700;
	color: #545454;
	font-size: 1.8rem;
}

.cart__totalprice{
	margin-top: 4rem;
	font-weight: 700;
	color: #545454;
	font-size: 2.1rem;
}

.cart__delete{
	display: flex;
	align-items: center;
	justify-content: center;
	width: 35px;
	height: 35px;
	background: #F44336;
	border-radius: 5px;
	margin-top: 2rem;
	cursor: pointer;
}

.cart__delete--icon, .cart__plus--icon, .cart__minus--icon{
	color: #fff;
	font-size: 2rem;
}

.cart__number--inner{
	display: flex;
	margin-top: 2rem;
}

.cart__number--inc{
	width: 45px;
	height: 45px;
	background: #595959;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.cart__number--text{
	width: 95px;
	background: #F5F5F5;
	display: flex;
	align-items: center;
	justify-content: center;
}

.cart__totalsum--inner{
	margin-top: 5rem;
	font-size: 2.4rem;
	font-weight: 600;
}

.cart__totalsum--text{
	color: #B0B0B0;
}

.cart__button{
	margin-top: 3rem;
	padding: 1.8rem 8rem;
	background: #FF9900;
	font-size: 2.4rem;
	font-weight: 600;
	color: #fff;
}

.card__size--inner{
	width: 100%;
	margin-top: 2rem;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.card__size--text{
	color: #C2C2C2;
	font-size: 2.1rem;
}

.card__size--img--inner{
	margin: 1rem 0 0 1rem;
	width: 40px;
	height: auto;
}

.card__size--img{
	width: 100%;
	height: auto;
}

// Оформление заказа

.radio{
	display: none;
}

.zakaz{
	padding: 3rem 0;
}

.zakaz__inner{
	width: 100%;
	display: flex;
	align-items: flex-start;
}

.zakaz__item--data{
	width: 45%;
	max-width: 480px;
	margin: 0 auto;
	padding: 0 1rem;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.zakaz__item--tovar{
	width: 55%;
	padding: 0 1rem;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.zakaz__data--input--inner{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 3rem;
	width: 100%;
	max-width: 480px;
}

.zakaz__data--input--inner:first-child{
	margin-top: 0;
}

.zakaz__error{
	width: 100%;
	text-align: right;
	margin-top: .5rem;
	font-size: 1.4rem;
	font-weight: 600;
	color: #F86363;
}

.zakaz__data--title{
	color: rgba(0, 0, 0, 0.78);
	font-size: 1.9rem;
	font-weight: 800;
}

.zakaz__label{
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin-top: 3rem;
	position: relative;
	padding-left: 4rem;
	width: 100%;
}

.zakaz__label:before{
	content: "";
	display: block;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background: #CCCCCC;
	position: absolute;
	top: 0;
	left: 0;
}

.radio:checked + .zakaz__label:before{
	background: #FF9900;
}

.zakaz__label--title{
	color: #393939;
	font-size: 1.9rem;
	font-weight: 700;
}

.zakaz__label:first-child{
	margin-top: 0;
}

.zakaz__label--text{
	margin-top: .5rem;
	font-size: 1.7rem;
	color: #AFAFAF;
	font-weight: 600;
}

.zakaz__input--area{
	resize: none;
	height: 160px;
}

.zakaz__title{
	width: 100%;
	text-align: left;
	margin-top: 3rem;
	color: rgba(0, 0, 0, 0.78);
	font-size: 1.9rem;
	font-weight: 800;
}

.zakaz__confirm--button{
	margin-top: 7rem;
	padding: 1.8rem 8rem;
	color: #fff;
	background: #FF9900;
	font-weight: 800;
	font-size: 2.4rem;
}

.zakaz__tovar--item{
	width: 100%;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	padding: 2rem 0;
	border-bottom: 1px solid #C7C7C7;
}

.zakaz__tovar--img--inner{
	width: 160px;
	height: auto;
	margin-left: 4rem;
}

.zakaz__tovar--img{
	width: 100%;
	height: auto;
}

.zakaz__tovar--text--inner{
	width: 100%;
	max-width: 270px;
	margin-left: 6rem;
}

.zakaz__tovar--title{
	font-size: 1.8rem;
	color: #545454;
	font-weight: 800;
}

.zakaz__tovar--point{
	color: #9E9E9E;
	font-size: 1.4rem;
	font-weight: 600;
	margin-top: .5rem;
}

.zakaz__tovar--price{
	color: #545454;
	font-size: 2.2rem;
	margin-top: 1rem;
	font-weight: 600;
}

.zakaz__tovar--fullprice--inner{
	width: 100%;
	padding: 2rem 0;
	border-bottom: 1px solid #c7c7c7;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.zakaz__tovar--fullprice--inner{
	color: #545454;
	margin-top: .3rem;
	font-weight: 600;
}

.zakaz__tovar--totalsum{
	color: #545454;
	font-weight: 700;
	margin-top: 2rem;
}

// Галлерея

.gallery{
	width: 100vw;
	height: 100vh;
	position: relative;
	overflow: hidden;
}

.gallery__logo--inner{
	width: 350px;
	height: auto;
	position: absolute;
	top: 50px;
	left: 50%;
	transform: translateX(-50%);
}

.gallery--logo{
	width: 100%;
	height: auto;
}

.gallery__item--max{
	width: 100vw;
	height: 100vh;
}

.gallery__img--max{
	width: 100vw;
	height: 100vh;
}

.gallery__arrow--inner{
	width: 65px;
	height: 65px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba(79, 79, 79, 0.43);
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 5;
}

.gallery__arrow{
	color: #fff;
}

.gallery__arrow--right{
	right: 30px;
}

.gallery__arrow--left{
	left: 30px;
}

.gallery__item--min{
	width: 192px!important;
	height: auto;
}

.gallery__inner--min{
	position: absolute!important;
	display: flex!important;
	justify-content: center;
	left: 50%;
	width: 100%!important;
	bottom: 0;
	transform: translateX(-50%);
	display: flex;
	z-index: 5;
}

.gallery__img--min{
	width: 100%;
	height: auto;
}

.gallery__point--inner{
	position: absolute;
	z-index: 10;
	width: 65px;
	height: 65px;
	border-radius: 50%;
	background: #4F4F4F;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: .2s linear;
}

.gallery__point--inner.gallery__point--bg{
	background: #FF9900;
}

.gallery__point--icon{
	color: #fff;
	transition: .2s linear;
}

.gallery__point--icon--transform{
	transform: rotate(45deg);
}

.gallery__point--text--inner{
	position: absolute;
	bottom: -30px;
	left: 30px;
	z-index: -1;
	padding: 1rem 2rem 3rem 2rem;
	width: 250px;
	display: none;
	flex-direction: column;
	align-items: center;
	background: #4F4F4F;
}

.gallery__point--text--inner.gallery__toggle--flex{
	display: flex;
}

.gallery__point--box{
	position: absolute;
	z-index: 10;
}

.gallery__point--title{
	color: rgba(255, 255, 255, 0.7);
	font-size: 1.7rem;
	font-weight: 800;
}

.gallery__point--text--box{
	width: 100%;
	margin-top: 1rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.gallery__point--name{
	color: #C1C1C1;
	font-size: 1.3rem;
}

.gallery__point--price{
	color: #C1C1C1;
	font-size: 1.3rem;
}

.gallery__point--button{
	margin-top: 2rem;
	width: 100%;
	padding: 1.5rem;
	border: 1px solid #A6A6A6;
	color: #A6A6A6;
}

.gallery__box1{
	top: 200px;
	left: 250px;
}

.gallery__box2{
	top: 300px;
	left: 650px;
}

.gallery__box3{
	top: 200px;
	right: 450px;
}

.gallery__box4{
	top: 400px;
	right: 650px;
}

.state__inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.state__item{
	width: 100%;
	display: flex;
}

.state__img--inner{
	width: 50%;
	height: auto;
}

.state__img{
	width: 100%;
	height: auto;
}

.state__content--inner{
	width: 50%;
	padding: 2rem 4rem;
}

.state__lightbrown--bg{
	background: #AC9B96;
}

.state__grey--bg{
	background: #8D8D8D;
}

.state__content--inner{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	color: #fff;
	text-align: center;
}

.state__title{
	font-weight: 700;
}

.state__text{
	margin-top: 3rem;
	font-size: 1.6rem;
}

.state__button{
	padding: 2rem 5rem;
	margin-top: 3rem;
	border: 2px solid #fff;
	color: #fff;
	font-size: 1.4rem;
}








