// Мобильные меню

.scroll{
	overflow: hidden;
}

.mobile__menu{
	width: 50vw;
	height: 100vh;
	background: #fff;
	position: fixed;
	z-index: 15;
	top: 0;
	left: 0;
	transform: translateX(-110%);
	box-shadow: 0 0 60px #d7d7d7;
	transition: .6s cubic-bezier(0,.71,1,1);
	padding: 6rem 4.5rem 2rem 4.5rem;
}

.mobile__menu--fade{
	transform: translateX(0%);
}

.mobile__menu--cross{
	font-size: 3.5rem;
	position: absolute;
	top: 20px;
	right: 20px;
	cursor: pointer;
}

.mobile__menu--inner{
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.mobile__menu--inner a{
	font-weight: 600;
	margin-top: 1rem;
	font-size: 2rem;
}



